/* App.css - SteepNSleep Dreamy Lavender Theme */

body {
  margin: 0;
  font-family: 'Quicksand', sans-serif;
  background-color: #f3e8ff; /* Soft lavender */
  color: #4b0082;
}

.App {
  text-align: center;
  padding: 2rem;
}

h1 {
  font-family: 'Pacifico', cursive;
  font-size: 3rem;
  font-weight: normal;
  color: #5a00a0;
  margin-bottom: 0.25rem;
  letter-spacing: 1px;
  text-shadow: 1px 1px 2px rgba(75, 0, 130, 0.2);
  position: relative;
}

/* Dreamy divider under the title */
h1::after {
  content: "";
  display: block;
  width: 80px;
  height: 4px;
  background-color: #b19cd9;
  margin: 0.5rem auto;
  border-radius: 2px;
}

h2 {
  font-family: 'Quicksand', sans-serif;
  font-size: 1.2rem;
  font-weight: 400;
  color: #7a42c1;
  margin-top: 0;
  margin-bottom: 1.5rem;
  opacity: 0.9;
}

button {
  background-color: #b19cd9;
  color: white;
  border: none;
  padding: 0.75rem 1.5rem;
  margin: 0.5rem;
  border-radius: 12px;
  box-shadow: 0 4px 6px rgba(106, 13, 173, 0.2);
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
  font-family: 'Quicksand', sans-serif;
}

button:hover {
  background-color: #a084ca;
  transform: scale(1.05);
}

input, select {
  padding: 0.5rem;
  border: 1px solid #d1c4e9;
  border-radius: 8px;
  margin: 0.5rem;
  background-color: #f8f5ff;
  color: #4b0082;
  font-family: 'Quicksand', sans-serif;
}

.chart-container {
  background-color: #e9d8fd;
  padding: 1rem;
  border-radius: 16px;
  margin-top: 2rem;
  box-shadow: 0 4px 8px rgba(106, 13, 173, 0.1);
}
